import { Twitter } from "lucide-react";
import Link from "next/link";

export function Navbar() {
    return (
        <nav className="fixed top-0 left-0 right-0 z-50 flex items-center justify-between px-6 py-4 border-b border-white/10 bg-black/50 backdrop-blur-md supports-[backdrop-filter]:bg-black/20">
            <div className="flex items-center gap-2">
                <div className="size-8 rounded-full bg-gradient-to-br from-white/20 to-transparent border border-white/10 flex items-center justify-center">
                    <div className="size-3 rounded-full bg-electric-blue shadow-[0_0_10px_var(--color-electric-blue)]" />
                </div>
                <span className="font-semibold text-white tracking-tight">The Bridge</span>
            </div>

            <Link
                href="https://twitter.com"
                target="_blank"
                className="p-2 text-white/70 hover:text-white transition-colors hover:bg-white/5 rounded-full"
            >
                <Twitter className="size-5" />
            </Link>
        </nav>
    );
}
