import { BridgeVisual } from "./BridgeVisual";
import { WaitlistForm } from "./WaitlistForm";
import { SocialProof } from "./SocialProof";

export function Hero() {
    return (
        <section className="min-h-screen flex flex-col items-center justify-center pt-24 pb-12 px-6 relative overflow-hidden">
            <div className="absolute inset-0 bg-[radial-gradient(circle_at_center,_var(--tw-gradient-stops))] from-electric-blue/10 via-transparent to-transparent opacity-50 blur-3xl pointer-events-none" />

            <div className="relative z-10 max-w-4xl w-full flex flex-col items-center text-center gap-8">
                <div className="relative">
                    <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 -z-10 h-[300px] w-[300px] rounded-full bg-electric-blue/20 blur-[100px]" />
                    <h1 className="text-5xl md:text-7xl font-bold tracking-tighter leading-[1.1]">
                        <span className="bg-clip-text text-transparent bg-gradient-to-b from-white to-white/60">
                            Stop AI Amnesia.
                        </span>
                    </h1>
                </div>

                <p className="text-lg md:text-xl text-white/60 max-w-2xl">
                    The missing link between your chat sessions. Keep your context alive across ChatGPT and Gemini.
                </p>

                <BridgeVisual />

                <div className="w-full max-w-md mt-8">
                    <WaitlistForm />
                </div>

                <SocialProof />
            </div>
        </section>
    );
}
